  type=\"password\"\r\n                        required\r\n                        placeholder=\"Verify Password\">\r\n          </b-form-input>\r\n        </b-form-group>\r\n        <b-button type=\"submit\" variant=\"primary\">Creat Account</b-button>\r\n        <b-button v-on:click=\"back\">Back</b-button>\r\n      </b-form>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\n  import { routes } from '../router/routes'\r\n  import axios from 'axios'\r\n\r\n  export default {\r\n    data() {\r\n      return {\r\n        routes,\r\n        collapsed: true,\r\n        user: {\r\n          mUsername: '',\r\n          mPassword: '',\r\n          mPasswordVerify: ''\r\n        },\r\n        message: ''\r\n      }\r\n    },\r\n    methods: {\r\n      // Creates a new user from form data\r\n      onSubmit(evt) {\r\n        evt.preventDefault();\r\n        if (this.user.mPassword !== this.user.mPasswordVerify) {\r\n          this.message = 'Passwords do not match'\r\n        } else {\r\n          axios.post('api/User/CreateUser', {\r\n            mUsername: this.user.mUsername,\r\n            mPassword: this.user.mPassword\r\n          }).then(response => {\r\n            this.message = response.data;\r\n            this.$router.push('/');\r\n          }, error => {\r\n            console.log(error.response)\r\n            this.message = error.response.data;\r\n          });\r\n        }\r\n      },\r\n      // Routes user back to main page\r\n      back() {\r\n        this.$router.push('/');\r\n      }\r\n    }\r\n  }\r\n</script>\r\n<style scoped>\r\n\r\n  .slide-enter-active, .slide-leave-active {\r\n    transition: max-height .35s\r\n  }\r\n\r\n  .slide-enter, .slide-leave-to {\r\n    max-height: 0px;\r\n  }\r\n\r\n  .slide-enter-to, .slide-leave {\r\n    max-height: 20em;\r\n  }\r\n</style>\r\n"],"sourceRoot":""}]);

